import * as React from "react";
import "../style/home.css"
import {
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow
} from "@material-ui/core";
import {withRouter} from "react-router-dom";

let md5 = require('md5');


let rows = []

class Home extends React.Component {
    componentWillMount() {
        this.getData()
    }

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <TableContainer  id="tableContainer" component={Paper}>
                <Table id="tableData">
                    <TableHead>
                        <TableRow>
                            <TableCell>Part Number</TableCell>
                            <TableCell>Count left </TableCell>
                            <TableCell>Delivery date</TableCell>
                            <TableCell>Delivery interval</TableCell>
                            <TableCell>Delivery size</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow key={row.partNumber}>
                                <TableCell component="th" scope="row">
                                    {row.partNubmer}
                                </TableCell>
                                <TableCell>{row.countLeft}</TableCell>
                                <TableCell>{row.deliveryDate}</TableCell>
                                <TableCell>{row.deliveryInterval}</TableCell>
                                <TableCell>{row.deliverySize}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        )
    }
    getData(){
        rows = [];
        var requestOptions = {
            method: 'GET',
            redirect: 'follow'
        };

        fetch("/agent/shipments?id=" + this.props.username+
            "&md5="+md5(this.props.password), requestOptions)
            .then(response => {
                return response.json()
            })
            .then(data => {
                // console.log(data)
                data.content.forEach(shipment=>{
                    rows.push(shipment)
                    console.log(shipment)
                })
                console.log(rows)
            })
            .catch(error => console.log('error', error));
    }
}
export default withRouter(Home)