import * as React from "react";
import {Button, Link, TextField} from '@material-ui/core';
import "../style/welcome.css"
import 'fontsource-roboto';
import {Redirect} from "react-router";
import {withRouter} from "react-router-dom";
import Cookies from 'universal-cookie';

let md5 = require('md5');

 class Welcome extends React.Component{

    constructor(props) {
        super(props);
        this.handleAuth = this.handleAuth.bind(this)
    }
    handleAuth(){
        var requestOptions = {
            method: 'POST',
            redirect: 'follow'
        };

        fetch("/agent/login?id=" + this.props.username +
            "&md5="+ md5(this.props.password), requestOptions)
            .then(response =>{if (response.status === 200){
                this.props.changeAuthstatus()
            }})
            .then(result => console.log(result))
            .catch(error => console.log('error', error));

    }



    render() {
        if (this.props.authStatus === true){
            return(
                <Redirect push to="/home"/>
            )
        }
        return (
            <div id="signInForm">
                <p>LaptopStore</p>
                <form>
                    <div>
                        <TextField
                        label="login"
                        value={this.props.username}
                        name="username"
                        required="true"
                        variant="outlined"
                        onChange={this.props.usernameChange}/>
                    </div>
                    <div>
                        <TextField
                        type="password"
                        label="password"
                        value={this.props.password}
                        name="password"
                        required="true"
                        variant="outlined"
                        onChange={this.props.passwordCHange}/>
                    </div>
                    <div id="buttons_bar">
                        <Button
                            onClick={this.handleAuth}
                            id="submit"
                            variant="contained"
                            color="primary"> SIGN IN
                        </Button>
                        <div id="registerDescription" >
                            <p>Don't have an account? </p>
                            <Link id="regLink">
                                Create new one
                            </Link>
                        </div>
                    </div>
                </form>
            </div>
        )
    }

}
export default withRouter (Welcome)

