import * as React from "react";
import {
    Route,
    Switch,
    withRouter
} from "react-router-dom"
import Home from "./Home";
import Welcome from "./Welcome";
import {Redirect} from "react-router";


class App extends React.Component {

    constructor(props) {
        super(props);
        this.state={
            username:"",
            password:"",
            authStatus:false
        }
        this.handleLoginChange = this.handleLoginChange.bind(this)
        this.handlePasswordChange= this.handlePasswordChange.bind(this)
        this.changeAuthStatus = this.changeAuthStatus.bind(this)
    }
    handleLoginChange(event) {

        const target = event.target;
        const value = target.value;
        this.setState({
            username:value
        })
    }
    handlePasswordChange(event) {

        const target = event.target;
        const value = target.value;
        this.setState({
            password:value
        })
    }

    changeAuthStatus(){
        this.setState({
            authStatus:true
        }
    )
    }

    render() {
        const {history} = this.props
        return (
            <div>
                {console.log(this.state)}
                <Switch>
                    <Route history={history} push path='/welcome'>
                        <Welcome
                            username = {this.state.username}
                            password = {this.state.password}
                            authStatus = {this.state.authStatus}
                            usernameChange = {this.handleLoginChange}
                            passwordCHange = {this.handlePasswordChange}
                            changeAuthstatus = {this.changeAuthStatus}
                        />
                    </Route>
                    <Route history={history} push path='/home' >
                        <Home username = {this.state.username}
                              password = {this.state.password}/>
                    </Route>
                    <Redirect form="/" to="/welcome"/>
                </Switch>
            </div>
        )
    }
}

export default withRouter(App)